import React from "react";

class ABookPage extends React.Component{
    state={
        "ov": false
    };
    render(){
        window.onscroll = function() {scrollFunction()};
        var scrollFunction = () => {
            if (document.body.scrollTop > 1 || document.documentElement.scrollTop > 3) {
                this.setState({"ov": true});
            }else{
                this.setState({"ov": false});
            }
        }
        return(
            <div className="aBookPage">
                <div className="left">
                    <div id="top">

                        <img src={booksArray[this.props.match.params.bookId].imageSrc} alt="SSSSSSsss"/>
                        <div id="content">
                            <h1> {booksArray[this.props.match.params.bookId].title} </h1>
                            <button id="startReading"> Start Reading </button><br/><br/>
                            <button id="editBook"> Edit Book </button>
                            <div className="bookList">
                            <p> <b>In the Booklist:</b> {booksArray[this.props.match.params.bookId].bookList} </p>
                            <button id="change"> Change </button>
                            </div>
                        </div>
                    </div>
                    <h3> {booksArray[this.props.match.params.bookId].description} </h3>
                </div>
                
                    <div>
                        <div className={this.state.ov ? "ov dbb": "ov"} onClick={() => {this.setState({"ov": false})}}></div>
                        <div className={this.state.ov ? "right": "right d"}>
                            <h1>More Options</h1>
                            <button id="editBookMetaData"> Edit Book Meta Data </button>
                            <button id="deleteBook"> Delete Book </button>
                        </div>
                        </div>
                <p className="scroll"> Scroll Down to See More Options </p>
            </div>
        );
    }
};

