import React from "react";
import PDF from "../../img/PDF.png";
import HTML from "../../img/HTML.png";
import EditBook from "../../img/EditBook.png";
import Star from "../../img/Star.png";
import AddToBookList from "../../img/AddToBookList.png";
import StartReading from "../../img/StartReading.png";
import Filters from "./Filters";
import {Link} from "react-router-dom";

class MyBooksPage extends React.Component{
    state={
        "ov": false
    };
    ov = () => {
        this.setState({"ov": false});
    };
    ovv = () => {
        this.setState({"ov": true});
    };
    render(){
        return(
            <div className="book_page">
                <div className="books_container">
                    {booksArray.map(function(item){
                        return <Book 
                            key={item.id}
                            bookId = {item.id - 1}
                            title={item.title}
                            description={item.description} 
                            chapters={item.chapters}
                            pages={item.pages}
                            type={item.type}
                            imageSrc={item.imageSrc}
                        />
                    })}
                </div>
                <button className="filter_btn" onClick={this.ovv}> Filters </button>
                
                <div className={this.state.ov ? "filters_ov": "filters_ov d"} onClick={this.ov}>
                    
                </div>
                <div className={this.state.ov ? "filter_container": "filter_container d"}>
                    <div className="filters">
                      <Filters/>
                    </div>
                </div>
            </div>
        );
    }
};

const Book = (props) => {
    return(
        <div className="book">
        <Link to={"/main/books/"+props.bookId} className="pagalHain">
            <div>
                <img src={props.imageSrc} alt="Book_COver" className="book_cover"/>
            </div>
            <div className="rightSection">
                <h2 className="book_title">{props.title}</h2>
                <div className="total_section">
                    <h6>Total Chapters: <b>{props.chapters}</b></h6>
                    <h6>Total Pages: <b>{props.pages}</b></h6>
                </div>
                <p className="description">{props.description}</p>
            </div>
            <div className="type">
                {(props.type === "PDF") ? <img src={PDF} alt="PDF" className="PDF"/>:<img src={HTML} alt="HTML" className="HTML"/>}
            </div>
        </Link>
            <div className="footerleft">
                <img src={EditBook} alt="Edit Book" className="editBook" />
                {(props.catergory === "faviorate") ? (<div className="faviorate"></div>):
                (<img src={Star} alt="Not Faviorate"className="nfaviorate"/>)}
                <img src={AddToBookList} alt="Add To Book List" className="atbl"/>
            </div>

            <Link to={"/main/books/readBook"+props.type+"/"+props.bookId} className="footerright">
                <img src={StartReading} alt="Start Reading" className="sr" />
            </Link>
            </div>
    )
};

