import React from "react";
import {Route, useLocation} from "react-router-dom";
import TopSection from "./TopSection";
import NewMenuBar from "./NewMenuBar";
import Dashboard from "./Dashboard";
import BooksPage from "../Books/BooksPage";
import ABookPage from "../Books/ABookPage";
import ReadBookPDF from "../Books/ReadBookPDF";
import CreateBook from "../Books/CreateBook/CreateBook";
import EditProfilePage from "../UserIdentification/EditProfilePage";
import BookLists from "../Books/BookLists";
import AllOptionsPage from "../Books/AllOptionsPage";

const MainController = () => {
    let location = useLocation();
    let path = location.pathname;
    return(
        <div className="bg-grey">
            <TopSection />
            <div className="dekho"></div>
            <div className="mainController">
                <NewMenuBar url={path}/>
                <div className="menu_bar_contents">
                    <div className="menu_bar_content">
                        <Route path="/main/" exact component={Dashboard}/>
                        <Route path="/main/books" exact component={BooksPage}/>
                        <Route path="/main/books/:bookId" exact component={ABookPage}/>
                        <Route path="/main/books/readBookPDF/:bookId" exact component={ReadBookPDF}/>
                        <Route path="/main/bookLists" exact component={BookLists}/>
                        <Route path="/main/allOptions" exact component={AllOptionsPage}/>
                        <Route path="/main/createBook" exact component={CreateBook}/>
                        <Route path="/main/editProfile" exact component={EditProfilePage}/>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default MainController;