import {BOOK_CREATED} from "../../../types";
import api from "../../../api";

export const bookCreated = (data) => ({
    type: BOOK_CREATED,
    data
})

export const createBook = (data) => dispatch => 
    api.books.createBook(data).then(msg => 
        dispatch(bookCreated({status: msg.status}))
    );

export const uploadBookCover = (data) => dispatch => 
    api.books.uploadBookCover(data).then(msg => 
        dispatch(bookCreated(msg))
    );
