import React from "react";

class Filters extends React.Component {
    state={
        data:{
            "sortBy": "None",
            "bookType": "None",
            "bookList": "None",
            "dateCreatedOne": "yy/mm/dd"
        }
    };
    onchange = event => this.setState({
        data: {...this.state.data, [event.target.name] : event.target.value }
    });
    render(){
        const {sortBy, bookType, bookList, dateCreatedOn} = this.state.data;
        return(
            <div className="filters">
                <div className="db">
                    <h4>Showing all of your books</h4>
                    <h5>You can Apply filters to change</h5>
                    <div id="sortBy">
                        <label> Sort By: </label>
                        <div></div>
                        <select onChange={this.onchange} value={sortBy} name="sortBy">
                            <option value="None"> None </option>
                            <option value="Date Created"> Date Created </option>
                            <option value="My Books"> Recently Viewed </option>
                        </select>
                    </div>
                    <h3>Filters</h3>
                    <div id="bookType">
                        <label> Book Type: </label>
                        <div></div>
                        <select onChange={this.onchange} value={bookType} name="bookType">
                            <option value="None"> None </option>
                            <option value="Date Created"> PDF </option>
                            <option value="My Books"> HTML </option>
                        </select>
                    </div>
                    <div id="bookList">
                        <label> In the Book List: </label>
                        <div></div>
                        <select onChange={this.onchange} value={bookList} name="bookList">
                            <option value="None"> None </option>
                            <option value="Date Created"> Computer Science </option>
                            <option value="My Books"> Class 4 </option>
                        </select>
                    </div>
                    <div id="dateCreatedOn">
                        <label> Date Created On: </label><br/>
                        <input type="date" onChange={this.onchange} value={dateCreatedOn} name="dateCreatedOn"/>
                    </div>
                </div>
            </div>
        );
    }
}

export default Filters;