import React from "react";
import Form1 from "./forms/form1";
import {createBook} from "../action/books.js";
import {connect} from "react-redux";
import {FaCheckCircle} from "react-icons/fa";
import {Link} from "react-router-dom";

class Page1 extends React.Component{
    state={
        modal: false
    }
    submit = (data) => {
        this.setState({loading: true});
        this.props.createBook(data)
            .then(msg => this.setState({modal: true, loading: false}));
    }
    closeModal = () => {
        this.setState({modal:false})
    }
    nextPage = () => {
        this.setState({modal:false})
        this.props.changePage();
    }
    render(){
        return(
            <div>
                {this.state.modal && (
                <div className="successc-modal-container">
                    <div className="successc-modal">
                        <FaCheckCircle  className="icon"/>
                        <h1> Congratulations </h1>
                        <h2> Book Created </h2>
                        <div>
                        <button onClick={this.nextPage} className="insert_contents"
                        >Insert The Contents Into Book</button>
                        <Link className="link" to="/main" onClick={this.closeModal}
                        >Or Go Back To Dashboard</Link>
                        </div>
                    </div>
                </div>)}
                <h1 className="content_title">Create Book</h1>
                {this.state.loading && (
                    <div className="loader">
                        <div></div>
                        <h1>Please Wait...</h1>
                    </div>
                    )}
                {this.props.bookId !== undefined && 
                <Form1 submit={this.submit} bookId={this.props.bookId}/>}
            </div>
        );
    };
};


function mapStateToProps (state) {
    return {bookId: state.user.totalBooks} 
}

export default connect(mapStateToProps, {createBook})(Page1);