import React from "react";
import Loading from "./Loading.gif";

class Form2 extends React.Component{
    constructor(props) {
      super(props);
    
      this.state = {
        data:{
            bookId: props.bookId,
            book_cover: props.book_cover,
            book_file: "",
        },
        fileUrl: "",
        imageUploading: false,
        failed: false,
        fs: "No File Choosen",
        fss: "No File Choosen",
      };
    };
    ptf = e => {
        const book_file = this.state.data.book_file;
        this.setState({data: {...this.state.data, book_fileName: book_file.name, book_fileType: book_file.type}});
    };
    filechange = e => {
        this.setState({imageUploading: true, failed: false})
        try{
        var files = e.target.files[0];
        this.setState({fileUrl: URL.createObjectURL(files),imageUploading: false, fs: files.name, data:{...this.state.data, book_cover:files}})
        }catch{
            this.setState({fileUrl: "",failed: true, imageUploading: false})
        }
    };
    filechangee = e => {
        try{
        var files = e.target.files[0];
        this.setState({fss: files.name, data:{...this.state.data, book_file:files}})
        }catch{
            this.setState({fileUrl: "",failed: true, imageUploading: false})
        }
    }
    onsubmit = (e) => {
        e.preventDefault();
    };
    uploadImage = () => {
        var formData = new FormData();
        formData.append('filetoupload', this.state.data.book_cover, this.state.data.book_cover.name)
        this.props.uploadBookCover(formData);
    };
    changeName = () => {
        this.props.changeName(this.state.data.name);
    };
    render(){
        const {book_cover, book_file,
            book_fileName, book_fileType} = this.state.data;
        const {imageUploading, failed, fileUrl, fs, fss} = this.state;
        const {filechange,filechangee, onsubmit} = this;
        return(
            <div className="form_comn">
                <form onSubmit={onsubmit}>
                    <div className="cfg">
                        <label>Upload the Book Cover: </label>

                            <div className="cfg2">
                        {!imageUploading ? fileUrl && (
                        <img 
                            src={fileUrl}
                            className="cpreview" 
                            alt="book_cover" 
                        />
                        ):<img 
                            src={Loading}
                            className="editGravatarr" 
                            alt="gravatar" 
                        />}
                        {failed && (
                            <h1 className="failecd">Failed</h1>
                        )}
                        <input 
                            type="file"
                            onChange={filechange}
                            accept=".png, .jpg, .bmp"
                            id="actual_btn"
                            hidden
                        /><label htmlFor="actual_btn" className="btn-filee">Choose File</label><span className="fs">{fs}</span></div>
                    </div><br/>
                    {book_cover && !failed && (
                    <button className="btn-upc" onClick={this.uploadImage}>Upload</button>)}<br/>
                    <div className="cfg2nd">
                        <label>Upload the Book File: </label>

                            <div className="cfg22nd">
                        <input 
                            type="file"
                            onChange={filechangee}
                            accept=".pdf"
                            id="actual_btnn"
                            hidden
                        /><label htmlFor="actual_btnn" className="btn-uploadBook">Choose File</label><span className="fs">{fss}</span></div>
                    </div><br/>
                    {book_file && (
                    <button className="btn-ptf" onClick={this.ptf}>Process the File</button>)}
                    {book_fileName && (
                        <div className="file_info">
                            <h1 className="text-center">Showing File Info</h1>
                            <p><b>Name: </b>{book_fileName}</p>
                            <p><b>Type: </b>{book_fileType}</p>
                        </div>
                    )}
                    {book_fileName && (
                    <button className="btn-upb" onClick={this.ptf}>Upload</button>)}
                </form>
            </div>
        );
    };
};

export default Form2;