import React from "react";
import Home from "../../img/Home.png";
import MyBooks from "../../img/MyBooks.png";
import MyBooklists from "../../img/MyBooklists.png";
import AllOptions from "../../img/AllOptions.png";
import BookWhite from "../../img/BookWhite.png";
import CreateBookListIK from "../../img/CreateBookListIK.png";
import Dashboard from "./Dashboard";
import BooksPage from "../Books/BooksPage";
import BookLists from "../Books/BookLists";
import ABookPage from "../Books/ABookPage";
import CreateBook from "../Books/CreateBook/CreateBook";



class MenuBar extends React.Component{
    state = {
        "active": "Dashboard",
        "createActive": false
    };
    clickD = () => {
        this.setState({"active": "Dashboard"});
    };
    clickMb = () => {
        this.setState({"active": "My Books"});
    };
    clickMbl = () => {
        this.setState({"active": "My Booklists"});
    };

    clickAll = () => {
        this.setState({"active": "All Options"});
    };

    createActive = () => {
        this.setState({"createActive": true});
    };
    createNActive = () => {
        this.setState({"createActive": false});
    };
    aCreateBook = () => {
        this.setState({"active": "Create Book", "createActive": false})
    }
    render(){
        return(
            <div>
                <div className="menu_bar_container">
                    <div className="menu_bar_buttons_container">
                    <div className="menu_bar_buttons">
                        <div 
                        className={(this.state.active === "Dashboard") ? "menu_bar_button active"
                        :"menu_bar_button"}
                         onClick={this.clickD}>
                            <img 
                                src={Home} 
                                alt="Dashboard" 
                                className="menu_bar_button_icon"
                            />
                            <h6 className="menu_bar_button_text">Dashboard</h6>
                        </div>
                        <div className={(this.state.active === "My Books") ? "menu_bar_button active"
                        :"menu_bar_button"} onClick={this.clickMb}>
                            <img 
                                src={MyBooks} 
                                alt="Dashboard" 
                                className="menu_bar_button_icon"
                            />
                            <h6 className="menu_bar_button_text">My Books</h6>
                        </div>
                        <div className={(this.state.active === "My Booklists") ? "menu_bar_button active"
                        :"menu_bar_button"} onClick={this.clickMbl}>
                            <img 
                                src={MyBooklists} 
                                alt="Dashboard" 
                                className="menu_bar_button_icon"
                            />
                            <h6 className="menu_bar_button_text">My Booklists</h6>
                        </div>
                        <div className={(this.state.active === "All Options") ? "menu_bar_button active"
                        :"menu_bar_button"} onClick={this.clickAll}>
                            <img 
                                src={AllOptions} 
                                alt="Dashboard" 
                                className="menu_bar_button_icon"
                            />
                            <h6 className="menu_bar_button_text">All Options</h6>
                        </div>
                    </div>
                    </div>

                    
                    <div className="menu_bar_contents">
                        {this.state.active==="Dashboard" && (
                            <div className="menu_bar_content">
                                <Dashboard />
                            </div>
                        )}
                        {this.state.active==="My Books" && (
                            <div className="menu_bar_content">
                                <BooksPage />
                            </div>
                        )}
                        {this.state.active==="My Booklists" && (
                            <div className="menu_bar_content">
                                <BookLists />
                            </div>
                        )}
                        {this.state.active==="All Options" && (
                            <div className="menu_bar_content ab">
                                <ABookPage book={Book}/>
                            </div>
                        )}
                    </div>

                    <div>
                        {this.state.active==="Create Book" && (
                            <div className="menu_bar_content">
                                <CreateBook />
                            </div>
                        )}
                        {this.state.active !== "Create Book" && ( 
                        <div>
                            {!this.state.createActive && (
                            <h1 className="createIcon" onClick={this.createActive}>
                             + 
                            </h1>  
                            )}
                            {this.state.createActive && (
                                <div>
                                    <div className="createOv" onClick={this.createNActive}>
                                    </div>
                                    <div id="createBookList">
                                    <div className="createBookListIcon">
                                        <img src={CreateBookListIK} alt="B" />
                                    </div> 
                                    <h1 id="text2"> Create Booklist </h1>
                                    </div>
                                    <div id="createBook" onClick={this.aCreateBook}>
                                    <div className="createBookIcon">
                                        <img src={BookWhite} alt="B" />
                                    </div> 
                                    <h1 id="text"> Create Book </h1>
                                    </div>
                                </div>
                            )}  
                        </div>
                        )}
                    </div>   
                </div>
            </div>
        );
    };
};

export default MenuBar;

const Book = {"id": "1","title": "Marigold", 
                "description": "This the Description of the Book. Lorem ipsum dolor sit amet, consectetur adipisicing elit. Lorem ipsum dolor sit amet, consectetur adipisicing elit. Lorem ipsum dolor sit amet, consectetur adipisicing elit. ",
                "chapters": "40", 
                "pages": "110", 
                "type": "PDF", 
                "bookList": "Class 4",
                "imageSrc": "https://bn1305files.storage.live.com/y4merdutAU-qnW7qtMPSd1DuBSPElKc0FFWiCThL37NYTPXjFKrYov2N0449feeMGA09BYufO9NlBkulweJm1APpKaY1Zn9-GJr95CArH-8iXmPARR6RzSV6oMqG6wEZw2w2Au2rHh7YmuU8G3Bjvv8paxXb2rxUfhXMHG8gANKuTO6g0Q1hTx4YpFuKOepLsEw?width=1500&height=2010&cropmode=none"};