import React from "react";
import Form2 from "./forms/form2";
import {uploadBookCover} from "../action/books.js";
import {connect} from "react-redux";

class page2 extends React.Component{
    state={
        loading: false
    }
    uploadBookCover = data => {this.setState({loading: true}); this.props.uploadBookCover(data).then(msg => this.setState({loading:false}))};
    render(){
        return(
            <div>
                <h1 className="content_title">Edit the Contents here</h1>
                {this.state.loading && (
                    <div className="loader">
                        <div></div>
                        <h1>Please Wait...</h1>
                    </div>
                    )}
                {this.props.bookId !== undefined && 
                <Form2 uploadBookCover={this.uploadBookCover} bookId={this.props.bookId} />}
            </div>
        );
    };
};

function mapStateToProps (state) {
    return {bookId: state.user.totalBooks} 
}

export default connect(mapStateToProps, {uploadBookCover})(page2);