import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {BrowserRouter, Route} from 'react-router-dom';
import * as serviceWorker from './serviceWorker';
import 'bootstrap/dist/css/bootstrap.min.css';
import "./index.css";
import { createStore, applyMiddleware} from 'redux'; 
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import api from "./api";
import { composeWithDevTools } from "redux-devtools-extension";
import rootReducer from './rootReducer';
import setUserAuthenticationToken from "./setUserAuthenticationToken";
import {userloggedIn} from "./components/UserIdentification/action";

const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(thunk))); 
setUserAuthenticationToken(localStorage.BookAppLogin);

if(localStorage.BookAppLogin){
    api.user.getUserData().then(user => {
        store.dispatch(userloggedIn(user))
    })
}


ReactDOM.render(
    <BrowserRouter>
        <Provider store={store}>
            <Route component={App}
            />
        </Provider>
    </BrowserRouter>
    , 
    document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
