import axios from "axios";
import setUserAuthenticationToken from "./setUserAuthenticationToken";

export default {
    user : {
        register: data =>
            axios.post("/api/user/new_user", {data} ).then(res=> {
                setUserAuthenticationToken(res.data.token.token);
                return res.data;
            }),
        getUserData: data => 
            axios.get("/api/user/getUserData", {data}, {withCredentials: true}).then(res=>res.data),
        login: data =>
            axios.post("/api/user/login", {data}).then(res=>{
                setUserAuthenticationToken(res.data.token.token);
                return res.data;
            }),
        uploadImage: data =>
            axios({
                method: 'post',
                url: '/api/user/uploadImage',
                data: data
            }).then(res=>res.data),
        changeName: data => 
            axios.post("/api/user/changeName", {data}).then(res=>res.data)
    },
    books: {
        createBook: data => 
            axios.post("/api/books/createBook", {data}).then(res=> {
                axios.defaults.headers.common['bookId'] = res.data.bookId;
                console.log(axios.defaults.headers)
                return res.data;
            }),
        uploadBookCover: data => 
            axios({
                method: 'post',
                url: '/api/books/uploadBookCover',
                data: data
            }).then(res=>res.data)
    }
}